import React from 'react';
import { useNavigate } from 'react-router-dom';

import Styles from './NotFound.module.css';
import cloudIcon from '../../../assets/icons/Cloud.png';
import op2miseLogo from '../../../assets/icons/op2mise.svg';

import FooterComponent from '../../../shared/footer/footer.component';

function NotFoundComponent() {
  const navigate = useNavigate();

  const navigateToHome = () => {
    navigate(`${process.env.REACT_APP_SUBFOLDER}/`);
  };

  return (
    <div
      style={{
        height: '100vh',
        width: '100vw',
        position: 'relative',
      }}
    >
      <div className={`${Styles.component} text-white text-center`}>
        <img className={Styles.logo} src={op2miseLogo} alt="op2miseLogo" />
        <p className={Styles.description1}>Something&apos;s wrong here...</p>
        <p className={Styles.description2}>
          We can&apos;t find the page you&apos;re looking for.
        </p>
        <img className={Styles.cloudIcon} src={cloudIcon} alt="Cloud-Icon" />
        <br />
        <button
          className={Styles.navigateButton}
          onClick={navigateToHome}
          type="button"
        >
          Home
        </button>
      </div>
      <FooterComponent />
    </div>
  );
}

export default NotFoundComponent;
